.navbar-nav {
  letter-spacing: 0.02em;
  font-family: 'vincente', serif;
  font-weight: bold;
}

.navbar-collapse a,
.navbar-collapse span {
  letter-spacing: 0.02em;
  font-family: 'vincente', serif;
  font-weight: 500;
  font-size: 23px;
  line-height: 1.2em;
  color: white;
  margin: 0 10px;
  white-space: nowrap;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.navbar-collapse a:hover {
  text-decoration: none;
  color: var(--wolves-orange);
}

.navbar-collapse span {
  color: var(--wolves-orange);
}

.navbar-collapse {
  justify-content: flex-start;
}

@media only screen and (min-width: 768px) and (max-width: 1160px) {
  .navbar-collapse {
    zoom: 0.95;
    flex-wrap: wrap;
  }
}

.logo {
  margin: 0px 10px;
}

.bg-wolf {
  background-color: var(--wolves-blue-dk) !important;
  padding: 8px;
}

.dp-conn-container {
  margin: 0px auto;
  min-width: 140px;
  min-height: 72px;
  color: white;
  font-size: 12px;
  display: flex;
}

.dp-conn-btn {
  padding: 6px;
  font-size: 15px;

  line-height: 1.1;
  white-space: normal;
  height: auto;
}

@media screen and (max-width: 767px) {
  .navbar-collapse a,
  .navbar-collapse span {
    margin: 2px 8px;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    /* font-size: 12px; */
  }
  .navbar-collapse a,
  .navbar-collapse {
    display: flex;
    flex-direction: column;
    justify-content: unset;
  }
}

@media screen and (max-width: 500px) {
  .navbar-collapse a,
  .navbar-collapse span {
    font-size: 26px;
  }

  .logo {
    width: 250px;
  }
}

.navbar-dark .navbar-brand {
  margin: 0 auto 8px auto;
}

.nav-item {
  cursor: pointer;
}

.dropdown-menu {
  margin: 0 !important;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu,
.dropdown-item:focus,
.dropdown-item:hover {
  background-color: transparent;
}

.dropdown-toggle {
  padding-left: 3px;
  padding-right: 3px;
}

.translateY_-10 {
  transform: translateY(-7px);
}

.dropdown-item.active {
  color: var(--wolves-orange);
  background-color: transparent;
  cursor: auto;
}

.dp-chain-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 16px;
  min-height: 100%;
}

.dp-chain-container .icon {
  display: inline-block;
  width: 32px;
  height: 32px;
  background-size: cover;
  cursor: pointer;
}

.dp-chain-container .polygon {
  background-image: url(../../assets/polygon.png);
}

.dp-chain-container .ethereum {
  background-image: url(../../assets/ethereum.png);
}

.dp-chain-container .selected {
  border: 1px solid var(--wolves-orange);
  cursor: default;
}
