  left: 4px;
}

#page4-content-text {
  display: flex;
  flex-basis: 48%;
  min-width: calc(98% - 512px);
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
}

.rarity-box {
  list-style: none;
  padding: 0px;
  margin-top: 16px;
}
.rarity-box h2 {
  line-height: 1;
}
.content-margin {
  margin: 0 8px;
}

.back-level-container {
  margin-top: 16px;
}

.back-level-container .c-pointer:hover {
  color: var(--wolves-orange);
}

@media all and (max-width: 800px) {
  #page4-content-container {
    justify-content: center;
  }

  #page4-content-image {
    flex-basis: 100%;
  }

  #page4-content-text {
    margin-top: 16px;
    flex-basis: 100%;
  }
}

.lock-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.lock-container div {
  text-align: center;
  flex-basis: calc(50% - 4px);
  border: 1px solid lightgray;
  margin: 2px;
  height: 26px;
}

.lock-container div.active {
  border: 2px solid var(--wolves-orange);
}

.lock-container div.select {
  cursor: pointer;
}

.lock-container div.full-width {
  flex-basis: 100%;
}

@media all and (max-width: 500px) {
  .modal-body input {
    font-size: 13px;
  }
}

