#currency-container {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}

#currency-container > div {
  flex-basis: 19%;
  text-align: center;
  padding: 4px;
  border: 1px solid var(--wolves-white);
  cursor: pointer;
}

#currency-container > div.selected {
  border: 2px solid var(--wolves-orange);
  background-color: var(--wolves-orange-transparent);
  cursor: default;
}

#currency-container > div.checked {
  border: 2px solid var(--wolves-orange);
}
