.wolves-btn {
  border: black 1px solid;
  background-color: var(--wolves-orange);
  color: var(--wolves-blue-dk);
  font-weight: 500;
  min-height: 32px;
  width: 100%;
}

.wolves-btn.white-border {
  border: white 1px solid;
}

.wolves-btn:disabled {
  color: var(--wolves-orange);
  background-color: var(--wolves-blue-dk);
  opacity: 0.5;
}

a.wolves-btn {
  padding-top: 4px;
}

a.wolves-btn:hover {
  text-decoration: none;
  color: var(--wolves-blue-dk);
}

.wolves-container {
  background-attachment: fixed;
  background-position-x: center;
  background-position-y: 0px;
  background-size: cover;
  position: relative;
  display: flex;
  flex-grow: 1;
  color: white;
  align-items: center;
  flex-direction: column;
  padding: 15px;
  min-height: 300px;
}

.wolves-container.bg-wolves {
  background-image: url(./assets/background_wolves.png);
}

.wolves-container.bg-bois {
  background-image: url(./assets/background_bois.jpg);
}

.wolves-container.bg-myPack {
  background-image: url(./assets/background_bois.jpg);
}

.wolves-header h2 {
  font-size: 30px;
  font-weight: 500;
}

.wolves-header h3 {
  font-size: 24px;
  font-weight: 500;
}

.wolves-header h4 {
  font-size: 14px;
}

.line-container {
  height: 8px;
  width: 85%;
  display: flex;
  align-items: center;
}

.line-container .line {
  height: 1px;
  width: calc(100% - 16px);
  background-color: var(--wolves-orange);
}

.line-container .dot {
  width: 8px;
  height: 8px;
  background-color: var(--wolves-orange);
  border-radius: 50%;
}

.wolves-border {
  border: var(--wolves-orange) 1px solid;
}

.bg-wolves,
.bg-lpInvestment {
  background-image: url(./assets/background_wolves.png);
}

.bg-bois,
.bg-myPack,
.bg-stableInvestment {
  background-image: url(./assets/background_bois.jpg);
}

.bg-flat2 {
  /* TODO::bg-asset */
  background-image: url(./assets/background_bois.jpg);
}

.bg-transparent-orange {
  background-color: var(--wolves-orange-transparent);
}

.wolves-orange-border {
  border: 1px solid #e8a688;
}

.wolves-text-shaddow {
  text-shadow: 2px 2px 2px #e7b198;
}

.wolves-input {
  color: inherit;
  height: 32px;
  width: 100%;
  vertical-align: bottom;
  background-color: var(--wolves-orange-transparent);
  border: 1px solid hsla(0, 0%, 0%, 0.699);
  outline: none;
}

.info-progress {
  display: block;
  height: 2px;
  background-color: whitesmoke;
  animation: progressBar 30s steps(30, end);
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  -webkit-animation: progressBar 30s steps(30, end);
  -webkit-animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
  -moz-animation: progressBar 30s steps(30, end);
  -moz-animation-fill-mode: both;
  -moz-animation-iteration-count: infinite;
}

.info-progress.absolute {
  position: absolute;
  bottom: 2px;
}

@keyframes progressBar {
  0% {
    width: calc(100% - 4px);
  }
  100% {
    width: 0;
  }
}

@-webkit-keyframes progressBar {
  0% {
    width: calc(100% - 4px);
  }
  100% {
    width: 0;
  }
}

@-moz-keyframes progressBar {
  0% {
    width: calc(100% - 4px);
  }
  100% {
    width: 0;
  }
}
